import {
	requestWB as uniRequest,
	requestZT as ztRequest
} from 'uni-request';


export default {
	data() {
		return {
			longitude: '',
			latitude: '',
			orderNo: '',
			params: {},
			id: '',
			isIphoneX: false,
			qrCodeOne: false,
		}
	},
	components: {

	},
	onLoad(option) {
		this._isIphoneX();
		this.getAddress(option);
		this.id = option.id
	},
	methods: {
		_isIphoneX() {
			uni.getSystemInfo({
				success: res => {
					// console.log('手机信息res'+res.model)
					let modelmes = res.model;
					if (modelmes.search('iPhone X') != -1) {
						this.isIphoneX = true;
					}
				},
			})
		},
		toticket() {
			let id = this.id
			console.log(id, '详情页')
			uni.reLaunch({
				url: '/pages/ticket/feizhuticket?id=' + id
			})
		},
		getAddress(option) {
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
					console.log(res);

					this.longitude = res.longitude;
					this.latitude = res.latitude;
					this.orderNo = option.orderNo || '';
					this.orderNo && this.getDetailData();
				},
				fail: (err) => {
					this.orderNo = option.orderNo || '';
					this.orderNo && this.getDetailData();
				}
			})


		},
		// 通过orderNo获取订单信息
		getDetailData() {
			uni.showLoading({
				title: '加载中'
			});
			ztRequest.post('/api/public/order/getOrderDetails', this.longitude ? {
				orderNo: this.orderNo,
				longitude: this.longitude,
				latitude: this.latitude
			} : {
				orderNo: this.orderNo
			}).then(res => {
				console.log(res)
				this.params = { ...res.data.content
				}
				
				if(res.data.content.transactionCodeUrl){
					if(res.data.content.transactionCodeUrl.indexOf(',')!==-1){
						this.params.transactionCodeUrl = res.data.content.transactionCodeUrl.split(',');
					} else {
						this.qrCodeOne = true
					}
				} 
				uni.hideLoading()
			});


		},
		statusType(status) { //状态0-取消 1-待支付 2-已支付 3-完结'
			switch (status) {
				case 0: //取消订单
					return '';
				case 1: //未支付
					return '待付款'
				case 2: //待使用
					return '待使用';
				case 3: //已使用
					return '已使用';
				case 5: //已使用
					return '订单失败';
				case 10: //已使用
					return '已关闭';
				default:
					return ''
			}
		},
		comeWayType(comeWay) { // 0 取票入园 1 电子票入园 2 身份证入园 3 优惠核销（整单优 惠)
			switch (comeWay) {
				case 0:
					return '取票入园'
				case 1:
					return '电子票入园'
				case 2:
					return '身份证入园'
				case 3:
					return '优惠核销（整单优惠)'
				default:
					return ''
			}
		},
		paymentType(payment) { // 支付方式  1-支付宝    2-微信
			switch (payment) {
				case 1:
					return '支付宝'
				case 2:
					return '微信'
				default:
					return ''
			}
		},
		
		ruleType(rule) {
			//console.log(rule)
			console.log(rule)
			// if(rule && !rule.length) {
			// 	return '暂无'
			// } else {
			// 	let ruleStr = '';
			// 	for(var i in rule) {
			// 		ruleStr+=rule[i].value;
			// 	}
			// 	if(i == rule.length - 1){
			// 		return ruleStr
			// 	}
			// }
		},
		// 唤起支付
		continuePayment() {
			let orderNo = this.orderNo
			uni.requestPayment({
				provider: 'alipay',
				orderInfo: this.params.outerId, //微信、支付宝订单数据
				success: function(res) {
					// console.log('success:' + JSON.stringify(res));
					uni.reLaunch({
						url: `/pages/orderCenter/orderDetail?orderNo=${orderNo}`
					})
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		},
		// 唤起呼我打车
		callTaxi() {
			// 跳转小程序
			/* uni.navigateToMiniProgram({
				appId: '2018122862752160',
				extraData: {
					'origin': this.longitude + ',' + this.latitude,
					'destination': this.params.longitude + ',' + this.params.latitude,

					'serviceType': 0
				},
				success: res => {
					// 打开成功
					// console.log(JSON.stringify(res));
				},
				fail: res => {
					// 打开失败
					console.log(JSON.stringify(res));
				}
			}) */
			
			// 跳转页面
			uni.navigateTo({
				url: `/pages/nothing/huerNothing`
			})
		},
		// 唤起电话退款
		callRefund() {
			uni.makePhoneCall({
				phoneNumber: this.params.servicePhone
			});
		}

	}
}
